.map {
	height: 100%;
	min-height: 400px; }

.contacts {
	padding-left: 10%; }

.contacts-item {
	margin-bottom: 35px;
	&:last-child {
		margin-bottom: 0; }
	&__title {
		font-weight: 600;
		margin-bottom: 10px;
		text-shadow: none; }
	.timework {
		line-height: 1.8; }
	.phones-list {
		display: flex;
		flex-direction: column;
		li {
			margin-bottom: 9px;
			&:last-child {
				margin-bottom: 0; }
			a {
				display: inline-flex;
				align-items: center;
				line-height: 1;
				min-width: 165px; } }
		.icon {
			display: inline-block;
			line-height: 1; } }
	address {
		margin: 0 0 4px; }
	.attention {
		margin: 10px 0 0;
		line-height: 1.3;
		font-size: 0.8em;
		color: #868686;
		max-width: 260px; } }

/*========== Media Desktop First  ==========*/
// ≥768px
@include media-breakpoint-down(sm) {
	.contacts {
		margin: 50px 0 0;
		display: flex;
		flex-wrap: wrap;
		&-item {
			flex-basis: 50%;
			max-width: 50%;
			padding-right: 15px; } } }

// ≥576px
@include media-breakpoint-down(xs) {
	.contacts {
		margin-top: 40px;
		&-item {
			flex-basis: 100%;
			max-width: 100%;
			padding-right: 0;
			margin-bottom: 22px; } } }
