.btn-close {
	font-size: 20px;
	opacity: 1;
	color: $color-close-icon; }

.mfp-close-btn-in .mfp-close {
	color: $color-close-icon; }

.selectize-input {
	border-radius: 0;
	box-shadow: none;
	color: $color-inputs-placeholder; }


input[type="radio"],
input[type="checkbox"] {
  -webkit-appearance: none;
  width: 16px;
  height: 16px;
  padding: 4px 1px;
  border: 1px solid #cacaca;
  border-radius: 3px;
  outline: none;
  margin: 0;
  box-sizing: border-box;
  box-shadow: none;
  cursor: pointer;
  background-color: #fff;
  &:checked:before,
  &:checked:before {
    content: "";
    display: block;
    width: 5px;
    height: 9px;
    border: 0 solid $accent;
    border-width: 0 1px 1px 0;
    transform: rotateZ(45deg);
    margin: -3px 4px;
    pointer-events: none;
    outline: none;
    box-shadow: none; }
  &:checked,
  &:checked {
    background-color: #fff;
    outline: none;
    border-color: $color-inputs-border; } }

.remove {
	font-family: 'icomoon';
	font-size: 1em;
	color: $color-close-icon;
	&:before {
		content: '\e901'; }
	&:hover {
		color: darken($color-close-icon, 10%); } }

.p-title {
	font-size: 1.13rem;
	text-transform: uppercase;
	font-weight: 500;
	text-shadow: 0 0 0 $text;
	margin-bottom: 25px; }

.btn-more {
	background: $color-btn-more-bg;
	display: block;
	text-align: center;
	padding: 10px;
	font-size: 0.9rem;
	text-shadow: 0 0 0 $text;
	margin: 15px 0; }

.page-content {
	min-height: 63vh; }

.descr {
	>* {
		margin-bottom: 1rem; } }

.clearfix {
	&:after {
		content: '';
		display: block;
		clear: both; } }

.content-text {
	h1, h2, h3, h4, h5, h6, p, ul, ol {
		margin: 0 0 0.9rem; }
	ul {
		padding: 0;
		line-height: 1.5;
		li {
			list-style-type: none;
			margin-bottom: 6px; } }
	ol {
		padding: 0 0 0 17px;
		line-height: 1.5;
		li {
			margin-bottom: 6px; } }
	a {
		text-decoration: underline; }

	&.delivery-oreder__content-text {
		ul {
			padding-left: 3%; } } }

.full-width {
	width: 100%; }

.product-info__availability {
	color: $color-product-availability;
	&:before {
		background: $color-product-availability; }
	&--available {
		color: $accent;
		&:before {
			background: $accent; } } }

/* ============ .top button ============ */
.top {
	position: fixed;
	bottom: 30px;
	background: #fff;
	border: 1px solid $accent;
	color: darken($accent, 10%);
	width: 30px;
	height: 30px;
	font-size: 1.1rem;
	line-height: 30px;
	text-align: center;
	cursor: pointer;
	right: -100px;
	z-index: 12;
	transition: all 0.15s ease;
	&:hover {
		background: $accent;
		color: $white; }
	&.active {
		right: 20px; } }
/* ============ /.top button ============ */

@import "base/media"; // Always at the end
